import { Component } from '@angular/core';
import {
    IonicPage, NavController, NavParams, ModalController, LoadingController, ViewController,
    AlertController, ActionSheetController, Platform
} from 'ionic-angular';
import { Api, Clan, Users } from '../../providers/providers';
import {Storage} from "@ionic/storage";
import {ClanEditPage} from "../clan/clanEdit";
import {ClanPage} from "../clan/clan";
import {ProfilePage} from "../profile/profile";
import {SocialSharing} from "@ionic-native/social-sharing";
import { Camera } from '@ionic-native/camera';


@IonicPage()
@Component({
    selector: 'page-clan',
    templateUrl: 'clanprofile.html',
})
export class ClanProfilePage {

    clan: any = {};
    isVisibleRankingClan: any;
    userClanRole: any;

    constructor(public navCtrl: NavController, 
        public navParams: NavParams,
        public storage: Storage,
        public api: Api,
        public modalCtrl: ModalController,
        public loadingCtrl: LoadingController,
        public viewCtrl: ViewController,
        public alertCtrl: AlertController,
        public actionSheetCtrl: ActionSheetController,
        public socialSharing: SocialSharing,
        public platform: Platform, 
        public camera: Camera,
        public clanProvider: Clan,
        public userProvider: Users) {


        let clanInput: any;
        clanInput = {};
        clanInput.clanId = this.navParams.get("clan");
        this.api.post('clan/get', clanInput)
        .subscribe(
            (data) => {
                this.clanProvider.setCurrentClan(JSON.parse(data.text()));
                this.clan = this.clanProvider.getCurrentClan();
                this.userClanRole = this.isUserAdmin();
            },
            (err) => {
            },
            () => {
                //this.goToHome();
            }
            );
    }

    ionViewDidEnter(){
    }

    showActionsClan(clanId){
    }

    showActions(userId, clanId, role){
        let data: any;
        data = {};
        data.userId = userId;
        data.clanId = clanId;
        const actionSheet = this.actionSheetCtrl.create({
            title: 'Actions',
            buttons: [{
                text: 'Annuler',
                role: 'cancel',
                handler: () => {
                }
            }]
        });
        if(role == 'ROLE_USER'){
            actionSheet.addButton(
            {
                text: 'Promouvoir',
                handler: () => {
                    data.role = 'ROLE_ADMIN';
                    this.api.post('clan/grant', data)
                    .subscribe(
                        (data) => {
                            let body: any;
                            body = JSON.parse(data.text());
                            this.clan = body;
                            this.isVisibleRankingClan = true;
                        },
                        (err) => {
                        },
                        () => {
                        }
                        );
                }
            }
            );    
        }   
        if(role == 'ROLE_ADMIN' || role == 'ROLE_CREATEUR'){
            actionSheet.addButton(
            {
                text: 'Rétrograder',
                handler: () => {
                    data.role = 'ROLE_USER';
                    this.api.post('clan/grant', data)
                    .subscribe(
                        (data) => {
                            let body: any;
                            body = JSON.parse(data.text());
                            this.clan = body;
                            this.isVisibleRankingClan = true;
                        },
                        (err) => {
                        },
                        () => {
                        }
                        );
                }
            });    
        }   

        actionSheet.addButton(
        {
            text: 'Exclure',
            handler: () => {
                this.api.post('clan/exclude', data)
                .subscribe(
                    (data) => {
                        let body: any;
                        body = JSON.parse(data.text());
                        this.clan = body;
                        this.isVisibleRankingClan = true;
                    },
                    (err) => {
                    },
                    () => {
                    }
                    );
            }
        });

        actionSheet.addButton({
            text: 'Bannir',
            handler: () => {
                this.api.post('clan/quit', data)
                .subscribe(
                    (data) => {
                        let body: any;
                        body = JSON.parse(data.text());
                        this.clan = body;
                        this.isVisibleRankingClan = true;
                    },
                    (err) => {
                    },
                    () => {
                    }
                    );
            }
        });

        actionSheet.present();
    }

    isUserAdmin(){
        for(let userClan of this.clan.userClans){
            if(userClan.id == this.userProvider.getId()){
                return userClan.role == 'ROLE_ADMIN' || userClan.role == 'ROLE_CREATEUR';
            }
        }
    }

    isNotMe(userId){
        return userId != this.userProvider.getId();
    }

    hasImage(){
        return this.userProvider.getImageAccount() != null;
    }

    back(){
        this.viewCtrl.dismiss();
    }

    goTo(page, direction) {
        this.navCtrl.push(page, {}, {
            direction: direction
        });
    }

    goToEditClan(clanId){
        this.navCtrl.push(ClanEditPage, {clan: clanId}, {direction: 'forward'});
    }

    close() {
        this.viewCtrl.dismiss();
    }

    showActionsClanQuit(){
        let data: any;
        data = {};
        data.userId = this.userProvider.getId();
        data.clanId = this.clan.id;
        let alert = this.alertCtrl.create({
            title: 'Quitter le clan',
            buttons: [
            {
                text: 'Annuler',
                role: 'cancel',
                handler: data => {
                }
            },
            {
                text: 'Quitter',
                handler: () => {
                    this.api.post('clan/quit', data)
                    .subscribe(
                        (data) => {
                            let body: any;
                            body = JSON.parse(data.text());
                            if(!body.error){
                                this.navCtrl.setRoot(ProfilePage);
                            }
                        },
                        (err) => {
                        },
                        () => {
                        }
                        );
                }
            }
            ]
        });
        alert.present();
    }

    sharePassword(clan){

        this.platform.ready().then(() => {

            var options = {
                message: 'Rejoins mon clan ' + clan.name + ' avec le mot de passe suivant : ' + clan.passCode+'. Rendez-vous sur Popme !',
                        subject: '', // fi. for email
                        files: [clan.image], // an array of filenames either locally or remotely
                        url: '',
                        chooserTitle: 'Mot de passe' // Android only
                    }

                    this.socialSharing.shareWithOptions(options)
                    .then(() => {
                            //this.showSuccessShareMsg();
                        })
                    .catch((err) => {
                            //this.showErrorShareMsg(err);
                        });


                });
    }

}


