import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, LoadingController, AlertController  } from 'ionic-angular';
import { Api, StorageProvider, Users, Rank } from '../../providers/providers';
import {Storage} from "@ionic/storage";
import {LoginPage} from "../login/login";
import {PoperProfilePage} from "../poperprofile/poperprofile";
import { TranslateService } from "@ngx-translate/core";
import {SettingsPage} from "../pages";

/**
 * Generated class for the LogPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

 @IonicPage()
 @Component({
  selector: 'page-relative-rank',
  templateUrl: 'relativeRank.html',
})
 export class RelativeRankPage {

  user: any;
  userPlace: any = {};
  allRanks: any[];
  ranks: any[];
  rankType: string;
  typeRank: number;
  name: any;
  clans: any[];
  clan: any;
  rankingClan: any;
  displayMenu: any;
  isDefaultRanking: boolean;
  delta: number = 10;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public api: Api,
    public storage: Storage,
    public storageProvider: StorageProvider,
    public loadingCtrl: LoadingController,
    public modalCtrl: ModalController,
    public alertCtrl: AlertController,
    public userProfile: Users,
    public rankProvider: Rank,
    public translate: TranslateService) {
    this.storage.get('isConnected').then((val) => {
      if (!val) {
        this.navCtrl.setRoot(LoginPage);
      }
    });


  }
  setDisplayMenu(menu){
    this.displayMenu = menu;
    this.onDisplayMenuChange(menu);
  }

  ionViewWillEnter(){
    this.storage.get('defaultRanking').then((rank) => {
      if(rank){
        this.getAllWorld(false, rank);
        this.setDisplayMenu(rank);    
      }else{
        this.getAllWorld(false, 1);
        this.setDisplayMenu(1);                
      }
    });

  }

  getAllWorld(personalized, type){
    let loading = this.loadingCtrl.create({
      spinner: 'crescent',
      content: 'Chargement des données ...'
    });
    loading.present();

    let data: any;
    data = {};
    data.all = personalized;
    if(personalized == true){
      this.typeRank = 1;
    }else{
      this.typeRank = 2;
    }
    this.storage.get('configurations').then((configurations) => {
      data.range = +configurations.ACTIVITY_RANGE_DAY.value;
    });

    this.userPlace = this.userProfile.getPlace();
    
    data.userId = this.userProfile.getId();

    this.api.post('ranking', data)
    .subscribe(
      (data) => {
        let body: any;
        body = JSON.parse(data.text());
        this.ranks = body;

        loading.dismiss();
      },
      (err) => {
        loading.dismiss();
        this.storageProvider.clearConnexionStorage();
        this.navCtrl.setRoot(LoginPage);
      },
      () => {
        //this.goToHome();
      }
      );

    data = {};
    data.userId = this.userProfile.getId();
    this.api.post('clans/get', data)
    .subscribe(
      (data) => {
        let body: any;
        body = JSON.parse(data.text());
        this.clans = body;
        for (let id in this.clans) {
          this.goToRankingClan(this.clans[id].id, id);
        }
      },
      (err) => {
      },
      () => {
      }
      )
  }

  getInfosClan(clanId){
    let data: any;
    data = {};
    data.clanId = clanId;
    this.api.post('clan/get', data)
    .subscribe(
      (data) => {
        let body: any;
        body = JSON.parse(data.text());
        this.clan = body;
      },
      (err) => {
      },
      () => {
      }
      );
  }


    createClan(name){
        let data: any;
        data = {};
        data.name = name;
        data.description = 'test';
        data.userId =this.userProfile.getId();

        let loading = this.loadingCtrl.create({
        spinner: 'crescent',
        content: 'Chargement...',
        dismissOnPageChange: true
        });

        loading.present();

        this.api.post('clan/create', data)
        .subscribe(
        (data) => {
          loading.dismiss();
        },
        (err) => {
          loading.dismiss();
        },
        () => {
        }
        );
    }

    setDefaultRanking(typeRanking){

      if(this.isDefaultRanking){
        this.storage.set('defaultRanking', typeRanking);

        // Chargement de la place de l'utilisateur dans son profil
        this.rankProvider.loadRank(this.userProfile.getUser(), typeRanking);
      }

    }

    goToRankingClan(clanId, index){
      if(clanId != null){
        let data: any;
        data = {};
        data.userId = this.userProfile.getId();
        data.clanId = clanId;


        this.api.post('ranking/clan', data)
        .subscribe(
          (data) => {
            let body: any;
            body = JSON.parse(data.text());
            this.clans[index].ranking = body.clan;
            this.getInfosClan(clanId);
          },
          (err) => {
          },
          () => {
          }
          );
      }
}

    isRanks(){
      if(this.ranks !== undefined){
        return this.ranks !== undefined;
      }
      return false;
    }

    goToHisProfile(userId){

        // Interdiction d'accéder à son profil vue VISITEUR
        if(userId == this.userProfile.getId()){
        return;
        }

        let data: any;
        data = {};

        let loading = this.loadingCtrl.create({
        spinner: 'crescent',
        content: 'Chargement de données ...',
        dismissOnPageChange: true
        });

        loading.present();

        this.api.post('getUserWithPop/' + userId + '/' + this.userProfile.getId(), data)
        .subscribe(
        (data) => {
        let body: any;
        body = JSON.parse(data.text());

        this.prepareModal(body);

        loading.dismiss();

        },
        (err) => {
        this.storage.remove('user');
        this.storage.remove('isConnected');
        this.navCtrl.push(LoginPage, {}, {animate: true, direction: 'forward'});
        loading.dismiss();
        },
        () => {
        //this.goToHome();
        }
        );

    }

    onDisplayMenuChange(menu){
      this.storage.get('defaultRanking').then((rank) => {
        this.isDefaultRanking = (rank == menu);
      });
    }

    prepareModal(user){
      let profileModal = this.modalCtrl.create(PoperProfilePage, { userProfile: user });
      profileModal.present();
    }

    ionViewDidEnter() {
      this.storage.get('defaultRanking').then((rank) => {
        if (rank){
          this.displayMenu = rank;
        }else{
          this.displayMenu = 1;
        }
      });
    }

    ionViewDidLoad() {
      //this.rankType = 'world';
    }

    pushToSettings() {
        this.navCtrl.push(SettingsPage, {}, {animate: true, direction: 'forward'});
    }

}



