import { Component } from '@angular/core';
import {
    IonicPage, NavController, App, AlertController, LoadingController,
    ViewController
} from 'ionic-angular';
import { Api, Translate  } from '../../providers/providers';
import {Storage} from "@ionic/storage";
import { ProfilePage } from "../profile/profile";
import { FirstStep1Page } from "../firstStep/firstStep1";
import { appSettings } from '../../app/app-settings';
/**
 * Generated class for the RegisterPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

 @IonicPage()
 @Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
 export class RegisterPage {

  email: any;
  password: any;
  usualName: any;
  birthday: any;
  gender: any;
  city: any;

  constructor(public navCtrl: NavController, 
    public app: App, 
    public api: Api, 
    public storage: Storage,
    public alertCtrl: AlertController, 
    public loadingCtrl: LoadingController,
    public viewCtrl: ViewController,
    public translate: Translate) {
    this.storage.get(appSettings.STORAGE_ATTRIBUTES.IS_CONNECTED).then((val) => {
      if (val) {

        this.storage.get(appSettings.STORAGE_ATTRIBUTES.FIRST_CONNEXION).then((valFirstCon) => {
          if(valFirstCon){
            this.navCtrl.setRoot(FirstStep1Page);              
          }else{
            this.navCtrl.setRoot(ProfilePage);       
          }
        });
      }
    });
  }

  ionViewDidLoad() {

  }

  back() {
        this.viewCtrl.dismiss();
  }

  goToProfile() {
    let data: any;
    data = {};
    data.email = this.email;
    data.password = this.password;
    data.usualName = this.usualName;
    data.birthday = this.birthday;
    data.gender = this.gender;
    data.city = this.city;

    let loading = this.loadingCtrl.create({
      spinner: 'crescent',
      content: 'Inscription...',
      dismissOnPageChange: true
    });

    loading.present();

    this.api.post('createUser', data)
    .subscribe(
      (data) => {
        let body: any;
        body = JSON.parse(data.text());

        if(body.error){
          loading.dismiss();
          this.doAlert(body.message.text);
        }else{
          this.storage.set('isConnected',true);
          this.storage.set(appSettings.STORAGE_ATTRIBUTES.USER, body.id);

          // On redirige l'utilisateur vers sa page si il est authentifié
          this.storage.set(appSettings.STORAGE_ATTRIBUTES.FIRST_CONNEXION, true);
          this.storage.set('tutoriel', true);
          this.navCtrl.setRoot(FirstStep1Page);
        }
      },
      (err) => {
        loading.dismiss();
        this.doAlert(err.message);
      //this.navCtrl.setRoot(this.navCtrl.getActive().component);
    },
    () => {
      //this.goToHome();
    }
    );
  }


  goTo(page) {
    this.app.getRootNav().setRoot(page, {}, {animate: true, direction: 'forward'})
    .then(() => {

    })
  }

  goBack(page) {
    this.viewCtrl.dismiss();
  }

  doAlert(message) {
    let alert = this.alertCtrl.create({
      title: 'Aïe!',
      subTitle: message,
      buttons: ['Ok']
    });

    alert.present();
  }

}



