import { Injectable } from '@angular/core';
import { Api } from '../api/api';
import { Storage } from "@ionic/storage";
import { StorageProvider } from "../storage/storage";

@Injectable()
export class Users {
	private id:number;

	private account_image:string;

	private base_score:any;

	private actual_score:number;

	private birthday:any;

	private dateServer:number;

	private lastPopup:number;

	private description:string;

	private enabled:boolean;

	private enabled_detail: string;

	private first_name:string;

	private last_name:string;

	private place:any;

	private popies:any[];

	private rank:any[];

	private sexe:string;

	private usual_name:string;

	private type_connexion:string;

	constructor(public api: Api,
		public storage: Storage,
		public storageProvider: StorageProvider) {
	}

	getId(){
		return this.id;
	}

	getDescription(){
		return this.description;
	}

	getImageAccount(){
		return this.account_image;
	}
	
	getBirthday(){
		return this.birthday;
	}

	getPlace(){
		return this.place;
	}

	getBaseScore(){
		return this.base_score;
	}

	getActualScore(){
		return this.actual_score;
	}

	getEnabled(){
		return this.enabled;
	}

	getFirstName(){
		return this.first_name;
	}

	getLastName(){
		return this.last_name;
	}

	getSexe(){
		return this.sexe;
	}

	getUsualName(){
		return this.usual_name;
	}

	getTimeWithoutNewPopy(){
		return this.dateServer - this.lastPopup;
	}

	getPopies(){
		return this.popies;
	}

	getRank(){
		return this.rank;
	}

	setUser(user){
		this.id = user.id;
		this.account_image = user.account_image;
		this.base_score = user.base_score;
		this.actual_score = user.actual_score;
		this.birthday = user.birthday;
		this.dateServer = user.dateServer;
		this.lastPopup = user.lastPopup;
		this.description = user.description;
		this.enabled = user.enabled;
		this.first_name = user.first_name;
		this.last_name = user.last_name;
		this.place = user.place;
		this.popies = user.popies;
		this.rank = user.rank;
		this.sexe = user.sexe;
		this.usual_name = user.usual_name;
		this.enabled_detail = user.enabled_detail;
		this.type_connexion = user.type_connexion;

		// Préchargement des images
		let imageAccount = new Image();
		imageAccount.src = this.account_image;

		for(let popy in this.popies){
			if(this.popies[popy].image){
				let imagePopy = new Image();
				imagePopy.src = this.popies[popy].image;
			}
		}
	}

	getUser(){
		return this;
	}

	isDeleted(){
		return !this.enabled && this.enabled_detail == "AD";
	}

	isFirstConnexion(){
		return !this.enabled && this.enabled_detail == "FC";
	}

	isFacebook(){
		return this.type_connexion == 'F';
	}
}



