import { Component } from '@angular/core';
import {IonicPage, LoadingController, ModalController, NavController, NavParams, Platform} from 'ionic-angular';
import {LoginPage} from "../login/login";
import {Api} from "../../providers/api/api";
import {PoperProfilePage} from "../poperprofile/poperprofile";
import {Storage} from "@ionic/storage";
import {RelativeRankPage} from "../rank/relativeRank";
import {PhotoViewer} from "@ionic-native/photo-viewer";
import { ProfilePage, PlayPage, LogPage } from "../pages";
import { TranslateService } from "@ngx-translate/core";

/**
 * Generated class for the TrendPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

 @IonicPage()
 @Component({
   selector: 'page-trend',
   templateUrl: 'trend.html',
 })
 export class TrendPage {

   user: any;
   popeds: any[];
   popers: any[];
   firstPopy: any;

   constructor(public navCtrl: NavController,
     public navParams: NavParams,
     public loadingCtrl: LoadingController,
     public api: Api,
     public modalCtrl: ModalController,
     public storage: Storage,
     public platform: Platform,
     public photoViewer: PhotoViewer,
     public translate: TranslateService) {

    this.platform.ready().then(() => {

      this.storage.get('isConnected').then((val) => {
       if (!val) {
         this.navCtrl.setRoot(LoginPage);
       }
     });

      this.storage.get('firstLaunchTrend').then((val) => {
        this.storage.remove('firstLaunchTrend');
        this.getTrendPops(val);
      });

    });
  }

  isReadyFirstPopy(){
   return this.firstPopy !== undefined;
 }

 goToHisProfile(userId){

  // Interdiction d'accéder à son profil vue VISITEUR
  if(userId == this.user.id){
   return;
 }

 let data: any;
 data = {};

 let loading = this.loadingCtrl.create({
   spinner: 'crescent',
   content: 'Chargement de données ...',
   dismissOnPageChange: true
 });

 loading.present();

 this.api.post('getUserWithPop/' + userId + '/' + this.user.id, data)
 .subscribe(
   (data) => {
     let body: any;
     body = JSON.parse(data.text());

     this.prepareModal(body);

     loading.dismiss();

   },
   (err) => {
     loading.dismiss();
   },
   () => {
     //this.goToHome();
   }
   );

}

prepareModal(user){
 let profileModal = this.modalCtrl.create(PoperProfilePage, { userProfile: user });
 profileModal.present();
}

prepareModalRanking(type){
 let profileModal = this.modalCtrl.create(RelativeRankPage, { userProfile: type });
 profileModal.present();
}

ionViewDidLoad() {
}

    pushToProfile() {
        this.navCtrl.push(ProfilePage, {}, {animate: true, direction: 'back'}
        );
    }
    pushToPlay() {
        this.navCtrl.push(PlayPage, {}, {animate: true, direction: 'back'}
        );
    }
    pushToLog() {
        this.navCtrl.push(LogPage, {}, {animate: true, direction: 'back'}
        );
    }

viewImg(){
  this.photoViewer.show(this.firstPopy.image, 'Popy', {share: true});
}
getTrendPops(force = false){

  this.storage.get('user').then((user) => {

    this.storage.get('trends').then((trends) => {

      if(user && !force && trends){
        this.user = user;
        this.popeds = trends[0];
        this.popers = trends[1];
        this.firstPopy = trends[2];
      }else{
        let loading = this.loadingCtrl.create({
          spinner: 'crescent',
          content: 'Chargement des données ...'
        });
        loading.present();
        let poped: any;
        poped = {};
        this.api.post('trend/poped', poped)
        .subscribe(
          (data) => {
            this.popeds= [];
            let body: any;
            body = JSON.parse(data.text());
            for(let i=0; i<body.length;i++){
              this.popeds[i]=body[i];
            }
            let poper: any;
            poper = {};
            this.api.post('trend/poper', poper)
            .subscribe(
              (data) => {
                this.popers= [];
                let body: any;
                body = JSON.parse(data.text());
                for(let i=0; i<body.length;i++){
                  this.popers[i]=body[i];
                }
                let popy: any;
                popy = {};
                this.api.post('trend/first-popy', popy)
                .subscribe(
                  (data) => {
                    this.firstPopy = JSON.parse(data.text());
                    this.storage.set('trends', [this.popeds, this.popers, this.firstPopy]);
                  },
                  (err) => {
                  },
                  () => {
                    //this.goToHome();
                  }
                  );
              },
              (err) => {
              },
              () => {
                //this.goToHome();
              }
              );
          },
          (err) => {
          },
          () => {
            //this.goToHome();
          }
          );
        loading.dismiss();
      }
    });
  });
}

doRefresh(event){
  this.getTrendPops(true);
  event.complete();
}
}



