import { Component } from '@angular/core';
import {
  App, IonicPage, NavController, NavParams, ViewController, AlertController, ActionSheetController,
  LoadingController, PopoverController, Platform, ModalController
} from 'ionic-angular';
import { Api, Users, Rank, Tutoriel, Translate } from '../../providers/providers';
import { ImagePicker } from '@ionic-native/image-picker';
import { Storage } from "@ionic/storage";
import {LoginPage, PopoverScorePage, PopoverRankPage, PopoverTimePage, PlayPage, LogPage, TrendPage, SettingsPage, RelativeRankPage, ClanPage, PreviewPage } from "../pages";
import { Camera, CameraOptions } from '@ionic-native/camera';
import { Globalization } from '@ionic-native/globalization';
import { appSettings } from '../../app/app-settings';
import { SocialSharing } from '@ionic-native/social-sharing';
import { PhotoViewer } from '@ionic-native/photo-viewer';
import { Screenshot } from '@ionic-native/screenshot';
import { TranslateService } from '@ngx-translate/core';
import {PoperProfilePage} from "../poperprofile/poperprofile";
import { Crop } from '@ionic-native/crop';
import { Base64 } from '@ionic-native/base64';

@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {

  user: any = {};
  rank: any = {};
  timerNextPopy: any;
  maxTime: number;
  remainingTime: string;
  hidevalue: boolean=true;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public imagePicker: ImagePicker,
    public app: App, 
    public api: Api,
    public storage: Storage,
    public alertCtrl: AlertController,
    public camera: Camera,
    public actionSheetCtrl: ActionSheetController,
    public loadingCtrl: LoadingController,
    public globalization: Globalization,
    public popoverCtrl: PopoverController,
    public socialSharing: SocialSharing,
    public photoViewer: PhotoViewer,
    public platform: Platform,
    public screenshot: Screenshot,
    public userProvider: Users,
    public rankProvider: Rank,
    public modalCtrl: ModalController,
    public tutoriel: Tutoriel,
    public translate: TranslateService,
    public crop: Crop,
    public base64: Base64) {//console.log(this.translate.getNumber(this.translate));

    this.storage.get('isConnected').then((val) => {
      if (!val) {
        this.navCtrl.setRoot(LoginPage);
      }
    });

    this.user = this.userProvider.getUser();
    this.storage.get(appSettings.STORAGE_ATTRIBUTES.FROM_LOADING).then((fromLoading) => {
      if(fromLoading){
        let loading = this.loadingCtrl.create({
          spinner: 'crescent',
          content: 'Chargement...',
          dismissOnPageChange: true
        });

        loading.present();
        this.storage.get('defaultRanking').then((rank) => {
          if(rank){
            this.rankProvider.loadRank(this.user, rank);            
          }else{
            this.rankProvider.loadRank(this.user, 1);
          }
        });
        this.rank = this.rankProvider.getModel();

        console.log(this.rank);
        loading.dismiss();

        this.storage.remove('fromLoading');
        this.storage.get('configurations').then((configurations) => {
          this.maxTime = +configurations['BLOCK_ADD_POPY_DURATION'].value - this.userProvider.getTimeWithoutNewPopy();
          this.hidevalue = false;
          this.remainingTime = this.determineTimeText()
          this.startTimer();
        });
        /*this.storage.get('tutoriel').then((tutoriel) => {
          if(tutoriel){*/
            /*tutoriel.setTutoriel();
            if(tutoriel.getCurrentStep() == 0){
              tutoriel.nextStep({});
            }*/
            
            //this.presentPopoverScore(evt);
        /*  }
      });*/
    }else{
      this.storage.get('firstLaunchProfile').then((val) => {
        this.storage.remove('firstLaunchProfile');
        this.loadUserInfo(this.userProvider.getId(), val);

      });
    }
  });
  }

  ionViewDidEnter(){
    this.rank = this.rankProvider.getModel();
  }

  getPreferredLanguage(){
    this.globalization.getPreferredLanguage()
    .then(res => alert(res.value))
    .catch(e => alert(e));
  }

  getLocaleName(){
    this.globalization.getLocaleName()
    .then(res => alert(res.value))
    .catch(e => alert(e));
  }

  loadUserInfo(idUser, force = false){

    this.storage.get('user').then((userStorage) => {
      if (userStorage && !force) {
        this.user = this.userProvider.getUser();

        this.storage.get('defaultRanking').then((rank) => {
          if(rank){
            this.rankProvider.loadRank(this.user, rank);            
          }else{
            this.rankProvider.loadRank(this.user, 1);                    
          }
        });

        this.rank = this.rankProvider.getModel();
      }else{
        let loading = this.loadingCtrl.create({
          spinner: 'crescent',
          content: 'Chargement de données ...',
          dismissOnPageChange: true
        });

        loading.present();

        let data: any;
        data = {};

        this.api.post('getUser/' + idUser, data)
        .subscribe(
          (data) => {
            let body: any;
            body = JSON.parse(data.text());

            this.userProvider.setUser(body);

            this.user = this.userProvider.getUser();

            this.storage.get('defaultRanking').then((rank) => {
              if(rank){
                this.rankProvider.loadRank(this.user, rank);            
              }else{
                this.rankProvider.loadRank(this.user, 1);                    
              }
            });

            this.storage.get('configurations').then((configurations) => {
              this.maxTime = +configurations['BLOCK_ADD_POPY_DURATION'].value - this.userProvider.getTimeWithoutNewPopy();

              if(this.maxTime>0){
                this.hidevalue = false;
                this.remainingTime = this.determineTimeText();
              }

              this.startTimer();
            });
          },
          (err) => {
            this.storage.remove('user');
            this.storage.remove('isConnected');
            this.navCtrl.setRoot(LoginPage);
          },
          () => {
            //this.goToHome();
          }
          );
        loading.dismiss();
      }
    });



  }

  startTimer(){
    this.timerNextPopy = setTimeout(x => 
    {
      if(this.maxTime <= 0) { }
        this.maxTime -= 1;

      if(this.maxTime>0){
        this.hidevalue = false;
        this.remainingTime = this.determineTimeText()
        this.startTimer();
      }

      else{
        this.hidevalue = true;
      }

    }, 1000);
  }

  isClasse(){
    if(this.rank !== undefined){
      return this.rankProvider.getNumber() !== undefined;
    }
    return false;
  }

  determineTimeText(){
    var days = Math.floor(this.maxTime / 86400);
    var hours = Math.floor((this.maxTime - (days * 86400)) / 3600);
    var minutes = Math.floor((this.maxTime - (hours * 3600)) / 60);
    var seconds = (this.maxTime - (hours * 3600) - (minutes * 60));
    if(days > 0){  
      return days + "j " + hours + "h ";
    }else{
      if(hours > 0){  
        return hours + "h " + minutes + "m ";
      }else{
        if(minutes > 0){
          return minutes + "m " + seconds + "s";
        }else{
          return seconds + "s";
        }
      }
    } ;
  }

  ionViewDidLoad() {
  }

  close() {
    this.viewCtrl.dismiss();
  }

  doRefresh(event){
    clearTimeout(this.timerNextPopy);
    this.loadUserInfo(this.userProvider.getId(), true);

    if(this.tutoriel.isTutoriel() && this.tutoriel.getCurrentStep() == 8){
      this.tutoriel.nextStep({});
    }

    event.complete();
  }

  openPhotoPicker(index) {
    if(this.user.popies[index].image){
      this.doAlert('Votre stock de popies est atteint (4/4)');
    }else{
      let actionSheet = this.actionSheetCtrl.create({
        title: 'Modifier votre album',
        buttons: [
        {
          text: 'Choisir une photo',
          handler: () => {
            const options: CameraOptions = {
              sourceType: 0,
              quality: 50,
              destinationType: this.camera.DestinationType.FILE_URI,
              encodingType: this.camera.EncodingType.JPEG,
              mediaType: this.camera.MediaType.PICTURE,
              correctOrientation: true
            }
            this.camera.getPicture(options).then((imageData) => {
              // CROP
                this.crop.crop(imageData, {quality: 75})
                    .then(newPath => {
                            //this.base64.encodeFile(newPath).then((base64File: string) => {
                                //console.log(base64File);
                                alert(newPath);
                                let base64Image = 'data:image/jpeg;base64,' + newPath.replace(/^file:\/\//, '');
                                alert(base64Image);

                                this.user.popies[index].image = base64Image;
                                alert('test2');
                                let data: any;
                                data = {};
                                data.popy = base64Image;

                                // TODO TEST HERE
                                //this.presentPopoverPreview(imageData);

                                let loading = this.loadingCtrl.create({
                                    spinner: 'crescent',
                                    content: 'Chargement ...',
                                    dismissOnPageChange: true
                                });
                                loading.present();

                                this.api.post('addPopy/' + this.user.id, data)
                                    .subscribe(
                                        (data) => {
                                            let body: any;
                                            body = JSON.parse(data.text());
                                            if(body.error){
                                                loading.dismiss();
                                                this.doAlert(body.message.text);
                                            }else{
                                                loading.dismiss();
                                                this.loadUserInfo(this.user.id);
                                            }
                                        },
                                        (err) => {
                                            loading.dismiss();
                                            this.doAlert(err.message);
                                        },
                                        () => {
                                        }
                                    );



                            /*}).catch(() => {
                                alert('Error reading file');
                            });*/




                        },
                        error => {
                            console.log("CROP ERROR -> " + JSON.stringify(error));
                            alert("CROP ERROR: " + JSON.stringify(error));
                        }
                    );



            }, (err) => {
              // Handle error
            });
          }
        },
        {
          text: 'Prendre une photo',
          handler: () => {
            const options: CameraOptions = {
              sourceType: 1,
              quality: 50,
              destinationType: this.camera.DestinationType.DATA_URL,
              encodingType: this.camera.EncodingType.JPEG,
              mediaType: this.camera.MediaType.PICTURE,
              correctOrientation: true
            }
            this.camera.getPicture(options).then((imageData) => {
              // imageData is either a base64 encoded string or a file URI
              // If it's base64 (DATA_URL):
              let base64Image = 'data:image/jpeg;base64,' + imageData;
              this.user.popies[index].image = base64Image;

              let data: any;
              data = {};
              data.popy = base64Image;


              let loading = this.loadingCtrl.create({
                spinner: 'crescent',
                content: 'Chargement ...',
                dismissOnPageChange: true
              });
              loading.present();

              this.api.post('addPopy/' + this.user.id, data)
              .subscribe(
                (data) => {
                  let body: any;
                  body = JSON.parse(data.text());
                  if(body.error){
                    loading.dismiss();
                    this.doAlert(body.message.text);
                  }else{
                    loading.dismiss();
                    this.loadUserInfo(this.user.id);
                  }
                },
                (err) => {
                  loading.dismiss();
                  this.doAlert(err.message);
                },
                () => {
                }
                );

            }, (err) => {
            });
          }
        },
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
          }
        }
        ]
      });
      actionSheet.present();
    }
  }


  removeImage(index) {
    let alert = this.alertCtrl.create({
      title: 'Supprimer cette photo',
      message: 'Tu veux vraiment supprimer cette photo ?',
      buttons: [
      {
        text: 'Annuler',
        role: 'cancel',
        handler: () => {
        }
      },
      {
        text: 'Supprimer',
        handler: () => {

          let loading = this.loadingCtrl.create({
            spinner: 'crescent',
            content: 'Suppression ...'
          });
          loading.present();

          let data: any;
          data = {};

          this.api.post('deletePopy/' + this.user.id 
            + '/' +this.user.popies[index].id, data)
          .subscribe(
            (data) => {
              let body: any;
              body = JSON.parse(data.text());
              if(body.error){
                loading.dismiss();
                this.doAlert(body.message.text);
              }else{
                loading.dismiss();
                this.loadUserInfo(this.user.id);
              }
            },
            (err) => {
              loading.dismiss();
              this.doAlert(err.message);
              //this.navCtrl.setRoot(this.navCtrl.getActive().component);
            },
            () => {
              //this.goToHome();
            }
            );

            this.user.popies[index].image = "";
        }
      }
      ]
    });
    alert.present();
  }

  presentPopoverScore(event){
    console.log(event);
    let popover = this.popoverCtrl.create(PopoverScorePage);
    popover.present({
      ev: event
    });
  }

  presentPopoverRank(event){
    console.log(event);
    let popover = this.popoverCtrl.create(PopoverRankPage);
    popover.present({
      ev: event
    });
  }

  presentPopoverTime(event){
    console.log(event);
    let popover = this.popoverCtrl.create(PopoverTimePage);
    popover.present({
      ev: event
    });
  }

  goTo(page, direction) {
    this.navCtrl.setRoot(page, {},
      {animate: true, direction: direction}
      );
  }

    pushToPlay() {
        this.navCtrl.push(PlayPage, {}, {animate: true, direction: 'forward'}
        );
    }
    pushToLog() {
        this.navCtrl.push(LogPage, {}, {animate: true, direction: 'forward'}
        );
    }
    pushToTrend() {
        this.navCtrl.push(TrendPage, {}, {animate: true, direction: 'forward'}
        );
    }
    pushToSettings() {
        this.navCtrl.push(SettingsPage, {}, {animate: true, direction: 'forward'}
        );
    }
    pushToRelativeRank() {
        this.navCtrl.push(RelativeRankPage, {}, {animate: true, direction: 'forward'}
        );
    }
    pushToClan() {
        this.navCtrl.push(ClanPage, {}, {animate: true, direction: 'forward'}
        );
    }

  doAlert(message) {
    let alert = this.alertCtrl.create({
      title: 'Aïe!',
      subTitle: message,
      buttons: ['Ok']
    });

    alert.present();
  }

  viewImg(index){
    if(this.user.popies[index].image){
      this.photoViewer.show(this.user.popies[index].image, 'Popy', {share: true});
    }
  }

  shareViaSMS(){

    this.platform.ready().then(() => {
      // Take a screenshot and get temporary file URI
      this.screenshot.URI(100)
      .then((res) => {

        var options = {
          message: 'Regarde mon profil Popme sur ',
          subject: '', // fi. for email
          files: [res.URI], // an array of filenames either locally or remotely
          url: 'https://popme.app/loading',
          chooserTitle: 'Partage ton profil' // Android only
        }

        this.socialSharing.shareWithOptions(options)
        .then(() => {
          //this.showSuccessShareMsg();
        })
        .catch((err) => {
          //this.showErrorShareMsg(err);
        });

      }, (err) => {

      });
    });
  }

    presentPopoverPreview(img){
        let profileModal = this.modalCtrl.create(PreviewPage, { img: img });
        profileModal.onDidDismiss(coucou => {

        });
        profileModal.present();
    }


}


