import { Component } from '@angular/core';
import {
	App, IonicPage, NavController, NavParams, ViewController, AlertController, ActionSheetController,
	LoadingController
} from 'ionic-angular';
import { Api, Tutoriel } from '../../providers/providers';
import { ImagePicker } from '@ionic-native/image-picker';
import {Storage} from "@ionic/storage";
import { Camera } from '@ionic-native/camera';
import { appSettings } from '../../app/app-settings';
import {PhotoViewer} from "@ionic-native/photo-viewer";

@IonicPage()
@Component({
	selector: 'page-poperprofile',
	templateUrl: 'poperprofile.html',
})
export class PoperProfilePage {

	userProfile: any = {};
	userRank: any = {};
	popies: number[] = [];

	constructor(public navCtrl: NavController,
		public navParams: NavParams,
		public viewCtrl: ViewController,
		public imagePicker: ImagePicker,
		public app: App, 
		public api: Api,
		public storage: Storage,
		public alertCtrl: AlertController,
		public camera: Camera,
		public actionSheetCtrl: ActionSheetController,
		public loadingCtrl: LoadingController,
		public photoViewer: PhotoViewer,
		public tutoriel: Tutoriel) {
		this.userProfile = this.navParams.get("userProfile");
		this.userRank = this.userProfile.rank;

		if(tutoriel.isTutoriel()){
			tutoriel.nextStep({step: 6});
		}
	}

	back() {
		this.viewCtrl.dismiss(this.popies);
		if(this.tutoriel.isTutoriel()){
			this.tutoriel.nextStep({step: 7});
		}
	}

	closeModal(){
	}

	like(index) {
		if(!this.userProfile.popies[index].popped){
			this.storage.get(appSettings.STORAGE_ATTRIBUTES.USER).then((userId) => {
				let pop: any;
				pop = {};

				this.api.post('pop/' + userId + '/' + this.userProfile.popies[index].id, pop)
				.subscribe(
					(pop) => {
						this.userProfile.popies[index].popped = true;
						this.popies.push(this.userProfile.popies[index].id);
					},
					(err) => {
                    //this.navCtrl.setRoot(this.navCtrl.getActive().component);
                },
                () => {
                  //this.goToHome();
              }
              );
			});
		}
	}

	viewImg(index){
		if(this.userProfile.popies[index].image){
			this.photoViewer.show(this.userProfile.popies[index].image);
		}
	}

	dislike(index) {
		if(!this.userProfile.popies[index].popped){
			this.storage.get(appSettings.STORAGE_ATTRIBUTES.USER).then((userId) => {
				let pop: any;
				pop = {};

				this.api.post('unpop/' + userId + '/' + this.userProfile.popies[index].id, pop)
				.subscribe(
					(pop) => {
						console.log("unpop réalisé")
						this.userProfile.popies[index].popped = true;
						this.popies.push(this.userProfile.popies[index].id);
					},
					(err) => {
                  //this.navCtrl.setRoot(this.navCtrl.getActive().component);
              },
              () => {
                  //this.goToHome();
              }
              );
			});
		}
	}
}



