import { Component } from '@angular/core';
import { ViewController, AlertController } from 'ionic-angular';
import { Tutoriel } from '../../providers/providers';

@Component({})
export class TutorielBase {
	constructor(public viewCtrl: ViewController,
		public alertCtrl: AlertController,
		public tutoriel: Tutoriel) {}

	next(){
		this.tutoriel.nextStep({});
	}

	quit() {
		const confirm = this.alertCtrl.create({
			title: 'Quitter le tutoriel',
			message: 'Souhaites-tu vraiment quitter le tutoriel ?',
			buttons: [
			{
				text: 'Non',
				handler: () => {
				}
			},
			{
				text: 'Oui',
				handler: () => {
					this.end();
				}
			}
			]
		});
		confirm.present();
	}

	end(){
		this.viewCtrl.dismiss();
		this.tutoriel.quitTutoriel();
	}

	close() {
		this.viewCtrl.dismiss();
	}

}


