import { Component, ViewChild } from '@angular/core';
import {
    App, IonicPage, NavController, NavParams, ViewController, AlertController, LoadingController,
    ActionSheetController, Platform
} from 'ionic-angular';
import { Api, GameProvider, StorageProvider, Users, Rank, Translate } from '../../providers/providers';
import { ImagePicker } from '@ionic-native/image-picker';
import { Storage } from "@ionic/storage";
import { LoginPage, ProfilePage, FirstStep1Page, FirstStepFbPage } from "../pages";
import { Camera, CameraOptions } from '@ionic-native/camera';
import { Slides } from 'ionic-angular';
import { appSettings } from '../../app/app-settings';
import { GoogleAnalytics } from '@ionic-native/google-analytics';

@IonicPage()
@Component({
    selector: 'page-loading',
    templateUrl: 'loading.html',
})
export class LoadingPage {
    @ViewChild('slides') slides: Slides;
    currentSlide: number = 0;
    progressBar: number = 1;

    constructor(public navCtrl: NavController,
        public navParams: NavParams,
        public viewCtrl: ViewController,
        public imagePicker: ImagePicker,
        public app: App, 
        public api: Api,
        public storage: Storage,
        public alertCtrl: AlertController,
        public camera: Camera,
        public actionSheetCtrl: ActionSheetController, 
        public loadingCtrl: LoadingController,
        public users: Users,
        public storageProvider: StorageProvider,
        public gameProvider: GameProvider,
        public rankProvider: Rank,
        public ga: GoogleAnalytics,
        public platform: Platform,
        public translate: Translate) {
        this.ga.startTrackerWithId('UA-129861313-1')
        .then(() => {
            console.log('Google analytics is ready now');
            this.ga.trackView('Loading');
            this.ga.trackEvent('Loading', 'Lancement');
        })
        .catch(e => console.log('Error starting GoogleAnalytics', e));
    }

    ionViewDidLoad() {
        this.platform.ready().then(() => {
            this.storage.get(appSettings.STORAGE_ATTRIBUTES.IS_CONNECTED).then((val) => {
                if (!val) {
                    this.navCtrl.setRoot(LoginPage);
                }else{
                    console.log("loading");
                    this.slides.lockSwipes(true);
                    this.loadingInfos();
                }
            });
        });
    }

    close() {
        this.viewCtrl.dismiss();
    }

    loadingInfos(){
        this.storage.set('firstLaunchProfile', true);
        this.storage.set('firstLaunchLog', true);
        this.storage.set('firstLaunchTrend', true);
        this.storageProvider.loadConfigurations();

        this.storage.get(appSettings.STORAGE_ATTRIBUTES.USER).then(
            (user) => {
                let data: any;
                data = {};

                this.api.post('getUser/' + user, data)
                .subscribe(
                    (data) => {
                        let body: any;
                        body = JSON.parse(data.text());

                        this.users.setUser(body);

                        if(this.users.isDeleted()){
                            this.storageProvider.clearConnexionStorage();   
                            this.navCtrl.setRoot(LoginPage);
                        }else if(this.users.isFirstConnexion()){
                            if(this.users.isFacebook()){
                                this.navCtrl.setRoot(FirstStepFbPage);
                            }else{
                            this.navCtrl.setRoot(FirstStep1Page);
                            }
                        }else{
                            this.rankProvider.loadAllRanks();
                            this.storage.set(appSettings.STORAGE_ATTRIBUTES.FROM_LOADING, true);
                            let timer = setTimeout(x => {
                                this.gameProvider.loadPopies(20);
                                this.goToSlide();
                            }, 1500);

                            timer = setTimeout(x => {
                                this.navCtrl.setRoot(ProfilePage);
                            }, 3000);
                        }
                    });

            },
            (err) => {
                alert('Impossible de se connecter à internet. Veuillez vérifier votre connexion !');
                this.storageProvider.clearConnexionStorage();
                this.navCtrl.setRoot(LoginPage);
            }
          );
    }

    goToSlide() {
        this.currentSlide++;
        this.progressBar++;
        this.slides.lockSwipes(false);
        this.slides.slideTo(this.currentSlide, 400);
        this.slides.lockSwipes(true);
    }

    doAlert(message) {
        let alert = this.alertCtrl.create({
            title: 'Aïe!',
            subTitle: message,
            buttons: ['Ok']
        });

        alert.present();
    }
}



