import { Component, ViewChild } from '@angular/core';
import { Facebook } from '@ionic-native/facebook';
import { Storage } from "@ionic/storage";
import { IonicPage, NavController, ModalController, LoadingController, App, AlertController, Slides } from 'ionic-angular';
import { Api, StorageProvider, Translate } from '../../providers/providers';
import { LoadingPage, LoginClassicPage, RegisterPage } from "../pages";
import { ProfilePage } from "../profile/profile";
import { FirstStep1Page } from "../firstStep/firstStep1";
import { appSettings } from "../../app/app-settings";
import { TranslateService } from '@ngx-translate/core';

//import {page} from '../register/register';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  introSlides: any;
  profilePage = ProfilePage;
  user: any;
  password: any;
  loaded: boolean;
  isLoggedIn: boolean = false;
  usersFb: any;
  @ViewChild('slides') slides: Slides;

  constructor(
    public navCtrl: NavController, 
    public app: App, 
    public api: Api, 
    public storage: Storage, 
    public loadingCtrl: LoadingController, 
    public alertCtrl: AlertController, 
    public fb: Facebook,
    public storageProvider: StorageProvider,
    public modalCtrl: ModalController,
    public translate: TranslateService) {

    let tutorialMessage1 = '';
    let tutorialMessage2 = '';
    let tutorialMessage3 = '';
    this.translate.get('PAGE.LOGIN.TUTORIAL1').subscribe(value => { tutorialMessage1 = value;});
    this.translate.get('PAGE.LOGIN.TUTORIAL2').subscribe(value => { tutorialMessage2 = value;});
    this.translate.get('PAGE.LOGIN.TUTORIAL3').subscribe(value => { tutorialMessage3 = value;});

    this.introSlides = [
    {
      title: '<br/>',
      image: 'assets/img/intro/intro_1.png'
    },
    {
      title: tutorialMessage1,
      image: 'assets/img/intro/intro_2.png'
    },
    {
      title: tutorialMessage2,
      image: 'assets/img/intro/intro_3.png'
    },
    {
      title: tutorialMessage3,
      image: 'assets/img/intro/intro_4.png'
    }
    ]

    this.loaded = false;
    this.storage.get(appSettings.STORAGE_ATTRIBUTES.IS_CONNECTED).then((val) => {
      if (val) {
        this.navCtrl.setRoot(ProfilePage);
      }
    });
    this.loaded = true;

    this.storage.get('user').then((val) => {
      if (val) {
        this.user = val;
      }
    });

    this.checkLoginFb();
  }

  ionViewDidLoad() {

  }

  goTo(page) {
    this.app.getRootNav().setRoot(page, {}, {animate: true, direction: 'forward'})
    .then(() => {

    })
  }
    goTo2(page) {
        this.navCtrl.push(page);
    }

  pushRegister(){
    this.navCtrl.push(RegisterPage, {}, {
        direction: 'forward'
    });
  }

  prepareModal(){
    this.navCtrl.push(LoginClassicPage, {}, {
      direction: 'forward'
    });
  }

  checkLogin(){
    let data: any;
    data = {};
    data.user = this.user;
    data.password = this.password;

    this.api.post('connectUser', data)
    .subscribe(
      (data) => {
        let body: any;
        body = JSON.parse(data.text());

        if(body.error){
          if(body.message.key=='AUTHENTICATION_USER_NOT_ENABLED'){
            this.navCtrl.setRoot(FirstStep1Page);
          }else{
            this.showAlertBadAccount(body.message.text);
          }
        }else{
          this.storage.set('isConnected',true);
          this.storage.set('user', body.id);
          this.navCtrl.setRoot(LoadingPage);
        }
      },
      (err) => {
        let badAccount = '';
        this.translate.get('PAGE.LOGIN.BAD_ACCOUNT').subscribe(value => { badAccount = value;});
        this.showAlertBadAccount(badAccount);
        //alert("ko");
        //this.navCtrl.setRoot(this.navCtrl.getActive().component);
      },
      () => {
        //this.goToHome();
      }
      );
  }

  showAlertBadAccount(message) {
    const alert = this.alertCtrl.create({
      title: 'Erreur de connexion',
      subTitle: message,
      buttons: ['OK']
    });
    alert.present();
  }

  loginFB() {
    let data: any;
    data = {};
    this.fb.login(['public_profile', 'email'])
    .then(res => {
      //alert(res.authResponse.userID);
      if(res.status === "connected") {
        data.id = res.authResponse.userID;
        data.facebookId = res.authResponse.userID;
        //data.user = this.user;
        data.password = 'facebook';
        // Get user infos from the API
        this.fb.api("/me?fields=name,email,gender", []).then((userFB) => {
          // Get the connected user details
          data.usualName = userFB.name.split(' ').join('');
          data.email     = userFB.email;
          data.gender     = userFB.gender;
          this.api.post('connectFacebookUser', data)
          .subscribe(
            (data) => {
              let body: any;
              body = JSON.parse(data.text());
              //alert(body);
              // On redirige l'utilisateur vers sa page si il est authentifié
              this.storage.set('isConnected',true);
              this.storage.set('user', body.id);

              this.navCtrl.setRoot(LoadingPage);
            },
            (err) => {
              //alert(err);
              this.storageProvider.clearConnexionStorage();
              this.navCtrl.setRoot(LoginPage);
            },
            () => {
              //this.goToHome();
            }
            );
        });
      } else {
        this.isLoggedIn = false;
      }
    })
    .catch(e => console.log('Error logging into Facebook', e));
  }

  checkLoginFb(){
    this.fb.getLoginStatus()
    .then(res => {
      if(res.status === "connect") {
        alert('Point 8');
        this.isLoggedIn = true;
        // On redirige l'utilisateur vers sa page si il est authentifié
        this.navCtrl.setRoot(ProfilePage);
      } else {
        this.isLoggedIn = false;
      }
    })
    .catch(e => console.log(e));
  }

  getUserDetail(userid) {
    this.fb.api("/"+userid+"/?fields=id,email,name,picture,gender",["public_profile"])
    .then(res => {
      console.log(res);
      this.usersFb = res;
    })
    .catch(e => {
      console.log(e);
    });
  }

}



