import { Component } from '@angular/core';
import { App, IonicPage, NavController, NavParams, ViewController, AlertController, LoadingController } from 'ionic-angular';
import { Api } from '../../providers/api/api';
import { ImagePicker } from '@ionic-native/image-picker';
import { Storage } from "@ionic/storage";
import { LoginPage } from "../login/login";
import { LoadingPage } from "../loading/loading";

@IonicPage()
@Component({
  selector: 'page-first-step',
  templateUrl: 'firstStep3.html',
})
export class FirstStep3Page {

  userId: any;
  popy: string;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public imagePicker: ImagePicker,
    public app: App, 
    public api: Api,
    public storage: Storage,
    public alertCtrl: AlertController, 
    public loadingCtrl: LoadingController ) {
    this.storage.get('isConnected').then((val) => {
      if (!val) {
        this.navCtrl.setRoot(LoginPage);
      }
    });

    this.storage.get('user').then((val) => {
      if (!val) {
        this.navCtrl.setRoot(LoginPage);
      }else{
        this.userId = val;
      }
    });   
  }

  ionViewDidLoad() {
  }

  close() {
    this.viewCtrl.dismiss();
  }

  validProfileStep3(){

    let loading = this.loadingCtrl.create({
      spinner: 'crescent',
      content: 'Terminé ...',
      dismissOnPageChange: true
    });

    loading.present();
    let data: any;
    data = {};
    
    this.api.post('validInscription/' + this.userId, data)
    .subscribe(
      (data) => {
        let body: any;
        body = JSON.parse(data.text());
        if(body.error){
          loading.dismiss();
          this.doAlert(body.message.text);
        }else{
          this.storage.remove('firstConnexion');
          this.navCtrl.setRoot(LoadingPage);
        }
      },
      (err) => {
        loading.dismiss();
        this.doAlert(err.message);
        //this.navCtrl.setRoot(this.navCtrl.getActive().component);
      },
      () => {
        //this.goToHome();
      }
      );
  }

  doAlert(message) {
    let alert = this.alertCtrl.create({
      title: 'Aïe!',
      subTitle: message,
      buttons: ['Ok']
    });

    alert.present();
  }
}



